"use client";

import { useState } from "react";
import { TimelineItem } from "@/types/portfolio";

interface TimelineProps {
  items: TimelineItem[];
  className?: string;
  colors?: {
    background: string;
    text: string;
    accent: string;
    line: string;
    dot: string;
  };
}

export default function Timeline({ 
  items, 
  className = "",
  colors = {
    background: "bg-black",
    text: "text-white",
    accent: "text-blue-400",
    line: "bg-gray-600",
    dot: "bg-blue-500"
  }
}: TimelineProps) {
  const [isHovered, setIsHovered] = useState(false);

  return (
    <div className={`w-full overflow-hidden ${className}`}>
      <div 
        className="relative py-16"
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
      >
        {/* Timeline container with horizontal scroll animation */}
        <div 
          className={`transition-transform duration-1000 ease-in-out ${
            isHovered ? 'transform -translate-x-1/4' : 'transform translate-x-0'
          }`}
        >
          {/* Main timeline line */}
          <div className={`absolute top-1/2 left-0 right-0 h-0.5 ${colors.line} transform -translate-y-1/2`} />
          
          {/* Timeline items */}
          <div className="flex justify-between items-center relative min-w-full">
            {items.map((item, index) => (
              <div key={index} className="flex flex-col items-center relative">
                {/* Content above the line */}
                {(item.isAbove ?? index % 2 === 0) && (
                  <div className="mb-8 text-center max-w-48">
                    <h3 className={`font-bold ${colors.text} mb-2`}>{item.title}</h3>
                    <p className={`font-semibold ${colors.text} opacity-80 text-sm leading-relaxed`}>
                      {item.description}
                    </p>
                  </div>
                )}
                
                {/* Timeline dot */}
                <div className={`w-4 h-4 ${colors.dot} rounded-full border-4 border-gray-900 z-10 relative`}>
                  <div className={`absolute inset-0 ${colors.dot} rounded-full animate-pulse opacity-50`} />
                </div>
                
                {/* Year label */}
                <div className="my-4">
                  <span className={`${colors.accent} font-bold text-lg`}>{item.year}</span>
                </div>
                
                {/* Content below the line */}
                {!(item.isAbove ?? index % 2 === 0) && (
                  <div className="mt-4 text-center max-w-48">
                    <h3 className={`font-bold ${colors.text} mb-2`}>{item.title}</h3>
                    <p className={`font-semibold ${colors.text} opacity-80 text-sm leading-relaxed`}>
                      {item.description}
                    </p>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
        
        {/* Gradient overlays for smooth edges */}
        <div className={`absolute left-0 top-0 bottom-0 w-16 bg-gradient-to-r ${colors.background} to-transparent pointer-events-none z-20`} />
        <div className={`absolute right-0 top-0 bottom-0 w-16 bg-gradient-to-l ${colors.background} to-transparent pointer-events-none z-20`} />
      </div>
    </div>
  );
}
