"use client";

import { useState } from "react";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { ContactProps } from "@/types/portfolio";

export default function Contact({
  title = "Get In Touch",
  email = "hello@example.com",
  location = "San Francisco, CA",
  socialLinks = [
    { name: "GitHub", url: "https://github.com" },
    { name: "LinkedIn", url: "https://linkedin.com" },
    { name: "Twitter", url: "https://twitter.com" }
  ],
  colors = {
    background: "bg-gray-900/50",
    text: "text-white",
    accent: "text-blue-400"
  }
}: ContactProps) {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [showSuccess, setShowSuccess] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);

    await new Promise(resolve => setTimeout(resolve, 1000));
    
    setShowSuccess(true);
    setTimeout(() => setShowSuccess(false), 3000);

    setFormData({ name: "", email: "", message: "" });
    setIsSubmitting(false);
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  return (
    <section id="contact" className={`py-20 px-4 ${colors.background}`}>
      <div className="max-w-4xl mx-auto">
        <h2 className={`text-4xl md:text-5xl font-bold text-center mb-16 ${colors.text}`}>
          {title}
        </h2>
        
        <div className="grid md:grid-cols-2 gap-12">
          <div>
            <h3 className={`text-2xl font-semibold mb-4 ${colors.text}`}>Let's work together</h3>
            <p className={`${colors.text} opacity-80 mb-6`}>
              I'm always interested in hearing about new projects and opportunities. 
              Whether you have a question or just want to say hi, I'll do my best to get back to you!
            </p>
            
            <div className="space-y-4">
              <div>
                <h4 className={`font-semibold mb-1 ${colors.text}`}>Email</h4>
                <a href={`mailto:${email}`} className={`${colors.accent} hover:opacity-80 transition-opacity`}>
                  {email}
                </a>
              </div>
              
              <div>
                <h4 className={`font-semibold mb-1 ${colors.text}`}>Location</h4>
                <p className={`${colors.text} opacity-80`}>{location}</p>
              </div>
              
              <div>
                <h4 className={`font-semibold mb-1 ${colors.text}`}>Social</h4>
                <div className="flex gap-4">
                  {socialLinks.map((link) => (
                    <a 
                      key={link.name} 
                      href={link.url} 
                      className={`${colors.accent} hover:opacity-80 transition-opacity`}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      {link.name}
                    </a>
                  ))}
                </div>
              </div>
            </div>
          </div>
          
          <Card className={`bg-gray-900 border-gray-800 p-6 ${colors.background}`}>
            {showSuccess && (
              <div className="mb-4 p-3 bg-green-900/50 border border-green-700 rounded-md">
                <p className="text-green-300 text-sm">Message sent! I'll get back to you as soon as possible.</p>
              </div>
            )}
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Input
                  type="text"
                  name="name"
                  placeholder="Your Name"
                  value={formData.name}
                  onChange={handleChange}
                  required
                  className="bg-gray-800 border-gray-700"
                />
              </div>
              
              <div>
                <Input
                  type="email"
                  name="email"
                  placeholder="Your Email"
                  value={formData.email}
                  onChange={handleChange}
                  required
                  className="bg-gray-800 border-gray-700"
                />
              </div>
              
              <div>
                <Textarea
                  name="message"
                  placeholder="Your Message"
                  value={formData.message}
                  onChange={handleChange}
                  required
                  rows={5}
                  className="bg-gray-800 border-gray-700 resize-none"
                />
              </div>
              
              <Button
                type="submit"
                disabled={isSubmitting}
                className="w-full"
              >
                {isSubmitting ? "Sending..." : "Send Message"}
              </Button>
            </form>
          </Card>
        </div>
      </div>
    </section>
  );
}
